
import { GoogleAuthProvider, signInWithPopup, createUserWithEmailAndPassword, updateProfile, signOut as firebaseSignOut, signInWithEmailAndPassword as firebaseSignInWithEmailAndPassword } from "firebase/auth";
import { doc, getDoc, setDoc } from "firebase/firestore";
import { auth, firestore } from "./firebase"; // Correctly import the initialized services
import { useRouter } from "next/navigation";

export async function signInWithGoogle(router: ReturnType<typeof useRouter>) {
  const provider = new GoogleAuthProvider();
  try {
    const result = await signInWithPopup(auth, provider);
    const user = result.user;

    const userDocRef = doc(firestore, "users", user.uid);
    const userDoc = await getDoc(userDocRef);

    if (!userDoc.exists()) {
      // New user
      await setDoc(userDocRef, {
        name: user.displayName,
        email: user.email,
        photoURL: user.photoURL,
        skillsToTeach: [],
        skillsToLearn: [],
        onboardingCompleted: false,
      });
      router.push('/onboarding');
    } else {
      // Existing user
      const userData = userDoc.data();
      if (userData.onboardingCompleted) {
        router.push('/');
      } else {
        router.push('/onboarding');
      }
    }
  } catch (error: any) {
    if (error.code !== 'auth/popup-closed-by-user' && error.code !== 'auth/cancelled-popup-request') {
      console.error("Error signing in with Google", error);
      throw error;
    }
  }
}

export async function signUpWithEmailAndPassword(email: string, password: string, displayName: string, photoURL?: string) {
  try {
    const userCredential = await createUserWithEmailAndPassword(auth, email, password);
    const user = userCredential.user;
    
    await updateProfile(user, { displayName, photoURL });
    
    const userDocRef = doc(firestore, "users", user.uid);
    await setDoc(userDocRef, {
      name: displayName,
      email: user.email,
      photoURL: photoURL || user.photoURL || null,
      skillsToTeach: [],
      skillsToLearn: [],
      onboardingCompleted: false,
    });
    // For email sign-up, we can assume they need onboarding.
    // The redirect will be handled by useAuthRedirect watching the user state.
  } catch (error) {
    console.error("Error signing up with email and password", error);
    throw error;
  }
}

export async function signInWithEmailAndPassword(email: string, password: string) {
  try {
    await firebaseSignInWithEmailAndPassword(auth, email, password);
  } catch(error) {
    console.error("Error signing in with email and password", error);
    throw error;
  }
}


export async function signOut() {
  try {
    await firebaseSignOut(auth);
  } catch (error) {
    console.error("Error signing out", error);
  }
}

    